import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HumSocPage } from './hum-soc.page';

const routes: Routes = [
  {
    path: '',
    component: HumSocPage
  },
  {
    path: 'workspace-hum',
    loadChildren: () => import('../../Pages/hum-soc/workspace-hum/workspace-hum.module').then( m => m.WorkspaceHumPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class HumSocPageRoutingModule {}
